import { useAsyncEffect } from "@andrewthehan/use-async-effect";
import { DependencyList, useMemo } from "react";

const beep = new Audio(
  "data:audio/wav;base64,"
);

/**
 * @param onRead return whether to continue scanning
 */
export function useNfcRead(
  onRead: (event: NDEFReadingEvent) => boolean,
  deps?: DependencyList
) {
  const abortController = useMemo(() => new AbortController(), []);
  const ndef = useMemo(() => new NDEFReader(), []);
  useAsyncEffect(async () => {
    await ndef.scan({ signal: abortController.signal });
    return () => abortController.abort();
  }, []);

  useAsyncEffect(async () => {
    ndef.onreading = (event: NDEFReadingEvent) => {
      beep.play();
      if (!onRead(event)) {
        abortController.abort();
      }
    };
    ndef.onreadingerror = (e) => console.error(e);
  }, [onRead, deps]);
}
